.btn {
  font-size: 1.8rem;
  color: white;
  cursor: pointer;
  border: none;
}
.form-btn {
  background-color: #fba919;
  padding: 1rem;
  border-radius: 1rem;
  width: 10rem;
}
.sign-in-btn {
  background-color: transparent;
}
.btn:disabled {
  background-color: rgb(205, 205, 205);
}
.prev-next-btn {
  border: none;
  background-color: transparent;
  font-size: 2rem;
  cursor: pointer;
}
.prev-next-btn:disabled {
  cursor: default;
}

@media (max-width: 26em) {
  .sign-in-btn {
    font-size: 1.6rem;
  }
}
